.dialogsApp {
    display: flex;
    justify-content: space-between;
}

.usersList {
    background-color: var(--lightGreen);
    /*min-height: 60vh;*/
    flex: 0 0 15%;
    border-radius: 4px;
    -webkit-box-shadow: -2px 2px 2px 0px rgba(189, 189, 189, 1);
    -moz-box-shadow: -2px 2px 2px 0px rgba(189, 189, 189, 1);
    box-shadow: -2px 2px 2px 0px rgba(189, 189, 189, 1);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    /*align-items: center;*/

}

.usersList div {
    flex: 0 0 50%;
}

.dialogs {
    display: flex;
}

.dialogsItem {
    flex: 1 1 15%;
    background-color: var(--lightGreen);
    display: flex;


}

.dialog {
    display: flex;
flex-direction: row;

}

.dialog:hover {
    background-color: var(--white);
}

.dialog NavLink {
    display: flex;
}

.messages {
    padding: 10px;
    flex: 1 1 85%;
}

.dialog a {
    color: var(--primery);
    font-family: 'Inter', sans-serif;
    text-decoration: none;
    padding: 0.2rem 0.5rem;
    flex: 1;
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.dialog a span {
    flex: 0 0 100%;
}

.dialog a:hover {
    font-weight: bold;
}