.userPhoto{
    width: 120px;
    height: 120px;
    border-radius: 50%;
}

.currentPage {
    font-weight: bold;
    display: inline;
    cursor:pointer
}
.page{
    padding: 1px;
    display: inline;
    cursor:pointer
}

